<template>
  <div class="app-container calendar-list-container" :style="{height: $store.state.app.client.height + 'px'}" @click="desktopClick">
    <div :style="{height: ($store.state.app.client.height - 160) + 'px',width: ($store.state.app.client.width - 300) + 'px'}" style="position: relative;top: 30px;left: 65px;">
        <div class="innerDesktop ui-droppable">
          <div class="desktopIcon ui-draggable ui-droppable" v-for="(app,index) in appList" @click="layerOpen(app)" :class="app.type==='1'?'isApp':''" :title="app.name" :style="appStyle(index)" style="position: absolute;margin: 0px;">
            <span class="icon" :class="app.type==='1'?'isApp':''">
              <div class="txInfo" :class="app.type==='1'?'isApp':''" v-if="app.msgCount > 0">{{app.msgCount}}</div>
              <img :src="app.icon" :class="app.type==='1'?'isApp':''"/>
            </span>
            <div class="text" :class="app.type==='1'?'isApp':''"><span :class="app.type==='1'?'isApp':''">{{app.name}}</span><s></s></div>
          </div>
        </div>
    </div>
    <!--任务栏-->
    <div class="vl-notify-task" ></div>
    <!--鼠标右键菜单-->
    <div id="smartMenu_body"  class="smart_menu_box">
      <div class="smart_menu_body">
        <ul class="smart_menu_ul">
          <li class="smart_menu_li" v-show="isApp"><a class="smart_menu_a">打开应用</a></li>
          <!-- 分割线 -->
          <li class="smart_menu_li_separate" v-show="isApp"></li>
          <!-- 应用移动桌面 -->
          <li class="smart_menu_li" v-show="isApp" :class="isHover" @mouseenter="isHover = 'smart_menu_li_hover'" @mouseout="isHover = ''">
            <div id="smartMenu_body_1" class="smart_menu_box"@mouseenter="isHover = 'smart_menu_li_hover'" >
              <div class="smart_menu_body"  @mouseenter="isHover = 'smart_menu_li_hover'">
                <ul class="smart_menu_ul"   @mouseenter="isHover = 'smart_menu_li_hover'">
                  <li class="smart_menu_li" @mouseenter="isHover = 'smart_menu_li_hover'">
                    <a class="smart_menu_a" @mouseenter="isHover = 'smart_menu_li_hover'">桌面1</a>
                  </li>
                  <li class="smart_menu_li" @mouseenter="isHover = 'smart_menu_li_hover'">
                    <a class="smart_menu_a" @mouseenter="isHover = 'smart_menu_li_hover'">桌面2</a>
                  </li>
                </ul>
              </div>
            </div>
            <a class="smart_menu_a"  @mouseenter="isHover = 'smart_menu_li_hover'">
              <i class="smart_menu_triangle" @mouseenter="isHover = 'smart_menu_li_hover'"></i>
              移动应用到
            </a>
          </li>

          <li class="smart_menu_li" v-show="isApp"><a class="smart_menu_a">移除应用</a></li>
          <li class="smart_menu_li" v-show="!isApp"><a class="smart_menu_a">显示桌面</a></li>
          <li class="smart_menu_li_separate" v-show="!isApp"></li>
          <li class="smart_menu_li" v-show="!isApp"><a class="smart_menu_a">切换风格</a></li>
          <li class="smart_menu_li" v-show="!isApp"><a class="smart_menu_a">桌面设置</a></li>
          <li class="smart_menu_li" v-show="!isApp"><a class="smart_menu_a">主题设置</a></li>
          <li class="smart_menu_li" v-show="!isApp"><a class="smart_menu_a">重置桌面</a></li>
          <li class="smart_menu_li_separate" v-show="!isApp"></li>
          <li class="smart_menu_li" v-show="!isApp"><a class="smart_menu_a">退出系统</a></li>
        </ul>
      </div>
    </div>
    <!-- 主页导航栏 -->
    <div id="leftBar" style="display: block;z-index: 45;">
      <div id="dockContainer" class="dock_container dock_pos_left">
        <div class="dock_middle">
          <div id="default_app" class="ui-droppable">
            <div class="desktopIcon ui-draggable ui-droppable" v-for="app in leftIcon" @click="layerOpen(app)" :id="'menu_'+ app.id" :title="app.name">
              <span class="icon">
                 <div class="txInfo" v-if="app.msgCount > 0">{{app.msgCount}}</div>
                <img :src="app.icon"/>
              </span>
              <div class="text"><span>{{app.name}}</span><s></s></div>
            </div>
          </div>
          <div class="default_tools"></div>
          <div class="default_tools"></div>
          <div id="start_block">
            <a title="开始" id="start_btn" @click="startShow = !startShow"></a>
            <div id="start_item" v-show="startShow">
              <ul class="item admin">
                <li>
                  <span class="adminImg" :style="{backgroundImage:'url('+user.avatar+')'}"></span>
                  {{user.username}}
                </li>
              </ul>
              <ul class="item">
                <li v-for="menu in startMenuList" :id="'item0menu'+ menu.id">
                  <span><img :src="menu.icon"/>{{menu.name}}</span>
                  <b></b>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import student from '@/views/modules/stu/student'
import servicenote from '@/views/modules/stu/service-note/index'
import exam from '@/views/modules/stu/batch/index'
import grade from '../../views/modules/stu/grade/index'
import graduation from '../../views/modules/stu/graduation/index'
import revisit from '../../views/modules/stu/revisit/index'

export default {
  name: 'layout',
  components: {
    student,
    exam,
    grade
  },
  data() {
    return {
      startMenuList: [
        {
          id: 3,
          name: '个人办公',
          icon: 'data:image/png;base64,',
          childItem: []
        },
        {
          id: 10,
          name: '行政办公',
          icon: 'data:image/png;base64,',
          childItem: []
        },
        {
          id: 11,
          name: '人力资源',
          icon: 'data:image/png;base64,',
          childItem: []
        },
        {
          id: 9,
          name: '知识交流',
          icon: 'data:image/png;base64,',
          childItem: []
        },
        {
          id: 58,
          name: '系统设置',
          icon: 'data:image/png;base64,',
          childItem: []
        }
      ],
      user: {
        username: 'admin',
        avatar: 'http://youhuogan.oss-cn-shenzhen.aliyuncs.com/1/pictures/20170415/30d15a8ab5dc45b0ae5cfe2916ad6917.jpg'
      },
      leftIcon: [
        {
          id: 'menu17',
          name: '短消息',
          content: student,
          msgCount: 1,
          icon: 'data:image/png;base64,'
        },
        {
          id: 'menu20',
          name: '工作日志',
          content: student,
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 'menu21',
          name: '日程安排',
          content: student,
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 'menu31',
          name: '任务管理',
          content: student,
          msgCount: 0,
          icon: 'data:image/png;base64,'
        }
      ],
      startShow: false,
      isApp: false,
      isHover: '',
      appList: [
        {
          id: 123,
          name: '学员管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 1,
          icon: 'data:image/png;base64,'
        },
        {
          id: 456,
          name: '学员缴费',
          content: servicenote,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 789,
          name: '考试安排',
          content: exam,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 777,
          name: '成绩登记',
          content: grade,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 888,
          name: '毕业学员',
          content: graduation,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 999,
          name: '学员回访',
          content: revisit,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 111,
          name: '员工管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 666,
          name: '员工管理',
          content: student,
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 465465,
          name: '员工管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 54654654,
          name: '员工管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 78894654,
          name: '员工管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 784561456,
          name: '员工管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 46542341,
          name: '员工管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 87454,
          name: '员工管理',
          content: student,
          type: '1',
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        },
        {
          id: 474541654,
          name: '添加',
          content: student,
          params: [],
          msgCount: 0,
          icon: 'data:image/png;base64,'
        }
      ]
    }
  },
  components: {
    student
  },
  computed: {
  },
  mounted() {
    var that = this
    document.oncontextmenu = function(ev) {
      var oEvent = ev || even
      var oUl = document.getElementById('smartMenu_body')
      var clazzNames = oEvent.toElement.className
      that.isApp = clazzNames.indexOf('isApp') > -1
      // 一定要加px，要不然chrom不认
      oUl.style.top = oEvent.clientY + 'px'
      oUl.style.left = oEvent.clientX + 'px'
      oUl.style.display = 'block'
      return false
    }
  },
  methods: {
    // 桌面点击事件
    desktopClick(e) {
      this.startShow = e.target.id === 'start_btn'
      document.getElementById('smartMenu_body').style.display = 'none'
    },
    // 应用坐标
    appStyle(i) {
      var h = this.$store.state.app.client.height - 160
      // 屏幕高度除以应用高度 取整表示一列可以显示多少个
      var count = parseInt(h / 113)
      // 当前索引 除以每列个数 取整 表示第几列
      var column = parseInt(i / count)
      // 当前索引 取模 为当前列第几个
      var row = i % count
      return {
        top: row * 113 + 'px',
        left: column * 131 + 'px'
      }
    },
    // 打开应用
    layerOpen(app) {
      this.$layer.open({
        type: 2,
        id: app.id, // title
        title: app.name, // title
        area: ['1024px', '600px'], // 默认 宽 高
        shadeClose: false, // 点击遮罩关闭
        tabIcon: app.icon, // 应用图标 任务栏显示
        shade: false, // 遮罩 默认不显示
        content: {
          content: app.content,
          parent: this, // 当前的vue对象
          data: app.params// props
        }
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "src/styles/mixin.scss";
.app-wrapper {
  @include clearfix;
  position: relative;
  height: 100%;
  width: 100%;
}
</style>



