<template lang="html">
    <div class="vl-notify-task" ></div>
</template>

<script>
    import helper from './helper/helper.js';

    export default {
        data() {
            return {
                id: '',
                type: 0, //0（alert默认）1（页面层）2（iframe层）3（loading）4（tips层）,5(msg)
                title: '信息',
                content: '',
                area: 'auto',
                offset: 'auto',
                icon: -1,
                btn: '确定',
                time: 0,
                shade: true,
                yes: '',
                cancel: ''
            }
        },
        props: {

        },
        methods: {
        },
    }
</script>

<style lang="css">
    .vl-notify-task {
        background-image: url("data:image/png;base64,");
        width: 100%;
        height: 45px;
        position: absolute;
        bottom: 0;
        left: 0;
    }
</style>



